import * as React from 'react';
import Home from './components/Home';
import Reader from './components/Reader';
import DATA from './assets/socialmedia';
import './styles/App.css';

function App() {
  const [expanded, setExpanded] = React.useState({ id: '' });
  function FooterIcon({ data }) {
    return (
      <div onClick={() => window.location.href = data.link} className="icon">
        {data.icon}
      </div>
    );
  }
  return (
    <div className="App">
      {expanded.id ? <Reader onContract={() => setExpanded({ id: '' })} data={expanded} /> :
        <div style={{ flex: 1, display: 'flex', flexDirection: 'column' }}>
          <div style={{ flex: 1 }} />
          <Home onExpand={(data) => setExpanded(data)} />
          <footer>
            <div className='top'>
              {DATA.map((item) => {
                return (
                  <FooterIcon data={item} />
                )
              })}
            </div>
            <div className='bottom'>
            </div>
          </footer>
        </div>}
    </div>
  );
}

export default App;
