function Image({ src, caption }) {
    return (
        <div style={{ display: 'flex', textAlign: 'center', justifyContent: 'center', alignItems: 'center', flexDirection: 'column', marginBottom: '10px' }}>
            <img src={src} id='image' alt={caption} />
            <p style={{ color: 'gray' }}>{caption}</p>
        </div>
    )
};

export default Image;