import LinearProgress, { linearProgressClasses } from '@mui/material/LinearProgress';
import { styled } from '@mui/material/styles';
import '../styles/Card.css';

const ProgressBar = styled(LinearProgress, {
    shouldForwardProp: (prop) => prop !== 'color'
})(({ theme, color }) => ({
    height: 10,
    borderRadius: 5,
    [`&.${linearProgressClasses.colorPrimary}`]: {
        backgroundColor: theme.palette.grey[800],
    },
    [`& .${linearProgressClasses.bar}`]: {
        borderRadius: 5,
        backgroundColor: color ? color : theme.palette.mode,
    },
}));

function Card({ data, progress, fade, onExpand }) {
    return (
        <div onClick={() => onExpand(data)} className={`card ${fade ? 'hide' : 'show'}`}>
            <div style={{ flex: 1 }} />
            <div style={{ flex: 12, display: 'flex', flexDirection: 'column' }}>
                <div className='heading' >
                    <div className='image'>
                        <img src={data.image} alt={data.name} />
                    </div>
                    <div style={{ flex: 8, display: 'flex', alignItems: 'center' }}>
                        <h2 style={{ color: data.color }}>{data.name}</h2>
                    </div>
                </div>
                {progress ? <div style={{ flex: 0.3 }}>
                    <ProgressBar value={data.confidence} variant='determinate' color={data.color} sx={{ mt: 1 }} />
                </div> : null}
                {progress ? <div style={{ flex: 0.5 }} /> : null}
                <div style={{ flex: 3, display: 'flex', overflow: 'hidden' }} >
                    <p>{data.description}</p>
                </div>
                <div style={{ flex: 0.5 }} />
            </div>
            <div style={{ flex: 1 }} />
        </div>
    )
}

export default Card;