import './shopscrap.css';

function ShopScrap() {
    return (
        <div id="shopscrap" className="content">
            <p>
                This wasn't originally my idea. It came as a work from a client who later dropped the idea. But I kept it alive and open sourced it.
                Why is this interesting? Well, have you <i>bullied</i> a NodeJS server? But this project literally does.
            </p>
            <p>
                But what is the idea? Nothing new, and I have a feeling you have used similar services before. Have you searched for a product online in multiple sites to get the best deal available on it?
                Or better, just use a service that does it for you?
            </p>
            <p>
                Services like these do exist, and they're fast because they've been worked on for years. What my client wanted was that they wanted to build an entirely new one from scratch.
                And I tried some stuff before arriving at a dedicated server.
            </p>
            <h5>Approaches</h5>
            <p>
                Firstly, I thought that it would be enough to make a standalone app with React Native. The way this would work is it would first take search query as input,
                and then, given a number of seller websites you'd like to search, it'd spin up that many <code>WebView</code>'s, and load the sites concurrently. Then, it would scrape the sites, and generate
                a list of the most cheapest options available, all on device itself. Now, if you're an Android developer, you might already be thinking, okay dude, React Native seriously? You could have just
                written this whole thing with Kotlin and it's coroutines... You're right, but then, the client wanted the app to be cross platform, and run on iOS too. So, I stuck to React Native.
            </p>
            <p>
                And yeah, as you might have guessed, this didn't go well since <code>react-native-webview</code> didn't support concurrent loading of websites. So, as the time schedule closed in, I hastily
                switched to a dedicated server for the task. And yes, of all the languages I knew, for some reason, I chose JavaScript. Stupid me tbh. So, with that, I made a hybrid system, one which used Crawlers
                on supported sites, and used plain web scraping for the others. Here's how it went. For each request, the server would spin up a new browser window in a new thread. It would then load the sites in
                the browser windows, scrape and get a list of products, and return it to the client where it's be sorted. This was obviously not a great way at all as it consumed too much compute resources per request.
                And the fact that this whole thing was JavaScript didn't make things easier at all.
            </p>
            <h5>Launch</h5>
            <p>
                Launch? Are you kidding me? While this was the best solution that I could come up with in 4 months, it was in no way suited for production. And anyway, the client pulled the plug on the project so
                I took it to my own hands. I open sourced it happily. 
            </p>
            <h5>Current status</h5>
            <p>
                The project is technically alive, or so I'd say, because it still lives on GitHub, probably the only place where it can live right now. I happily invite anyone ready to experiment with multithreading
                selenium webdriver workloads in Node and anyone intersted in creating sorting algorithms for the product list. I'll happily acknowledge your work. 
            </p>
            <p>
                If you wanna check it out, just <a onClick={() => window.location.href = "https://github.com/vishalkrishnads/ShopScrap"}>click here</a> to go to the project's repo.
            </p>
            <h5>Wrapping up</h5>
            <p>
                To be frank, this was one of the projects I'd label time waste. Yes, it did teach me a lotta stuff. But then, I'm not sure if I'm gonna need them. Because honestly, I just hate web automation and scraping
                at this point. I'm literally bored of it and would try to avoid a career involving it. I'd rather work on programming cars, or making self driving AI systems. They're just more close to the heart for me and
                a potential career option that I love. None of these require multithreading with Node and/or web automation.
            </p>
        </div>
    );
};
export default ShopScrap;