import * as React from 'react';
import { LocationOn, CalendarToday } from '@mui/icons-material';
import { useMediaQuery } from '@react-hook/media-query';
import { Projects, Skills } from './ScrollView';
import '../styles/Home.css';

function Home({ onExpand }) {

    const isPortrait = useMediaQuery('only screen and (max-width: 1000px)');
    const props = { color: 'gray', fontSize: '300%' }
    function Detail({ icon, name }) {
        return (
            <div className='detail'>
                {icon}
                <h3>{name}</h3>
            </div>
        );
    }

    const LandScape = () => {
        return (
            <div className='landscape'>
                <div style={{ flex: 1 }} />
                <div className='heading'>
                    <h1>Skills</h1>
                </div>
                <div className='content' >
                    <Skills onExpand={onExpand} />
                </div>
                <div className='heading'>
                    <h1>Projects</h1>
                </div>
                <div className='content' >
                    <Projects onExpand={onExpand} />
                </div>
            </div>
        )
    }


    const Portrait = () => {

        const Tabs = [
            {
                id: 1,
                name: 'Skills',
                description: 'Tools, mostly programming languages & frameworks, which I use & love.',
                component: <Skills onExpand={onExpand} />
            },
            {
                id: 2,
                name: 'Projects',
                description: 'Stuff that I have built, click on one to read more.',
                component: <Projects onExpand={onExpand} />
            }
        ];
        const [focusedTab, setFocused] = React.useState(Tabs[0]);

        const TabButton = ({ data }) => {
            return (
                <div className='tab'>
                    <div onClick={() => setFocused(data)} style={{ backgroundColor: data.id === focusedTab.id ? 'blue' : 'transparent' }} className='button'>
                        <h3>{data.name}</h3>
                    </div>
                </div>
            )
        }

        return (
            <div className='portrait' >
                <div style={{ flex: 0.5, display: 'flex' }}>
                    <div style={{ width: '10vw' }} />
                    {Tabs.map((item) => {
                        return (
                            <TabButton data={item} />
                        );
                    })}
                    <div style={{ width: '10vw' }} />
                </div>
                <div style={{ flex: 0.5, display: 'flex' }}>
                    <div style={{ flex: 1 }} />
                    <div style={{ flex: 10, display: 'flex', justifyContent: 'center', alignItems: 'center', textAlign: 'center' }}>
                        <p style={{ color: 'gray' }}>{focusedTab.description}</p>
                    </div>
                    <div style={{ flex: 1 }} />
                </div>
                <div style={{ flex: 2, display: 'flex' }} >
                    {focusedTab.component}
                </div>
            </div>
        );
    }

    return (
        <body>
            <div className='profile'>
                <div className='image-container' >
                    <img src={require('../assets/img/profile.png')} alt={'MyPic'} />
                </div>
                <div className='info-container' >
                    <div className='margin-top' />
                    <div style={{ flex: 1, display: 'flex' }}>
                        <div className='margin-left' />
                        <div style={{ flex: 1, display: 'flex', flexDirection: 'column' }}>
                            <h4>Hi there. I'm,</h4>
                            <h1>Vishal</h1>
                        </div>
                    </div>
                    <div className='info' >
                        <Detail icon={<CalendarToday sx={props} />} name={'19 y/o'} />
                        <Detail icon={<LocationOn sx={props} />} name={'India'} />
                    </div>
                    <div className='margin-bottom' />
                </div>
            </div>
            {isPortrait ? <Portrait /> : <LandScape />}
        </body>
    );
};

export default Home;
