import LinearProgress, { linearProgressClasses } from '@mui/material/LinearProgress';
import { styled } from '@mui/material/styles';
import { ArrowBackIosNew } from '@mui/icons-material';
import '../styles/Reader.css';
import React from 'react';

const ProgressBar = styled(LinearProgress, {
    shouldForwardProp: (prop) => prop !== 'color'
})(({ theme, color }) => ({
    height: 10,
    borderRadius: 5,
    [`&.${linearProgressClasses.colorPrimary}`]: {
        backgroundColor: theme.palette.grey[800],
    },
    [`& .${linearProgressClasses.bar}`]: {
        borderRadius: 5,
        backgroundColor: color ? color : theme.palette.mode,
    },
}));

function Reader({ onContract, data }) {
    const [progress, setProgress] = React.useState(0);

    React.useEffect(() => {
        window.onscroll = function () { myFunction() };

        var navbar = document.getElementById("progress");
        var sticky = navbar.offsetTop;

        function myFunction() {
            if (window.pageYOffset >= sticky) {
                navbar.classList.add("sticky")
            } else {
                navbar.classList.remove("sticky");
            }
            var winScroll = document.body.scrollTop || document.documentElement.scrollTop;
            var height = document.documentElement.scrollHeight - document.documentElement.clientHeight;
            setProgress((winScroll / height) * 100);
        }
    }, [])

    // React.useEffect(() => setTimeout(() => setProgress(100), 1000), [])
    return (
        <div style={{ flex: 1, display: 'flex', flexDirection: 'column' }} >
            <div className='headerbar'>
                <div className='header'>
                    <div onClick={onContract}>
                        <ArrowBackIosNew sx={{ fontSize: '300%', color: data.color }} />
                    </div>
                </div>
                <div style={{ flex: 10 }} />
            </div>
            <div className='reader'>
                <div style={{ flex: 1, minHeight: '10vh' }} />
                <div style={{ flex: 1, display: 'flex' }}>
                    <div style={{ flex: 1 }} />
                    <div className='heading'>
                        <h1>{data.name}</h1>
                        <h3>{data.caption}</h3>
                        <div id='progress'>
                            <ProgressBar value={progress} variant='determinate' color={data.color} sx={{ mt: 1 }} />
                        </div>
                    </div>
                    <div style={{ flex: 1 }} />
                </div>
                <div style={{ flex: 9, display: 'flex' }}>
                    <div className='margin' />
                    {data.component}
                    <div className='margin' />
                </div>
            </div>
            <div style={{ flex: 0.5, minHeight: '5vh' }} />
        </div>
    )
};

export default Reader;