import * as React from 'react';
import { ArrowRight, ArrowLeft } from '@mui/icons-material';
import Card from './Card';
import skills from '../assets/skills';
import projects from '../assets/projects';
import '../styles/ScrollView.css';

function ScrollView({ data, willExpand, onExpand }) {

    const [selected, setSelected] = React.useState(data[0]);
    const [fadeRef, fade] = React.useState(false);
    const arrayRef = React.useRef(0);

    function switchCard() {
        fade(true);
        setTimeout(() => {
            setSelected(data[arrayRef.current]);
            fade(false);
        }, 200)
    }

    React.useEffect(() => {
        fade(true);
        setTimeout(() => {
            arrayRef.current = 0;
            setSelected(data[arrayRef.current])
            fade(false);
        }, 200)
    }, [data])

    // swipe gestures for mobile devices
    React.useEffect(() => {
        let touchstartX = 0
        let touchendX = 0
        const slider = document.getElementById('scroller')
        function handleGesture() {
            if ((touchendX > touchstartX) && (arrayRef.current > 0)) {
                arrayRef.current--;
                switchCard();
            }
            if ((touchendX < touchstartX) && (arrayRef.current < data.length - 1)) {
                arrayRef.current++;
                switchCard();
            }
        }

        slider.addEventListener('touchstart', e => {
            touchstartX = e.changedTouches[0].screenX
        })

        slider.addEventListener('touchend', e => {
            touchendX = e.changedTouches[0].screenX
            handleGesture()
        })
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return (
        <div className='container'>
            <div className="scroller">
                <div className='button-container'>
                    {arrayRef.current === 0 ? null : <div onClick={() => {
                        arrayRef.current--;
                        switchCard();
                    }} className='button' >
                        <ArrowLeft sx={{ color: 'white', fontSize: '350%' }} />
                    </div>}
                </div>
                <div id="scroller" className='body'>
                    <Card onExpand={willExpand ? onExpand : null} fade={fadeRef} progress={selected.confidence ? true : false} data={selected} />
                </div>
                <div className='button-container'>
                    {arrayRef.current === data.length - 1 ? null : <div onClick={() => {
                        arrayRef.current++;
                        switchCard();
                    }} className='button' >
                        <ArrowRight sx={{ color: 'white', fontSize: '350%' }} />
                    </div>}
                </div>
            </div>
            <div className='indicators'>
                {data.map((item) => {
                    return (
                        <div style={{ color: selected.id === item.id ? 'white' : 'gray' }} className='indicator'><p>&#8226;</p></div>
                    );
                })}
            </div>
        </div>
    )
};

export function Projects({ onExpand }) {
    return (
        <ScrollView data={projects} willExpand={true} onExpand={onExpand} />
    );
};

export function Skills({ onExpand }) {
    return (
        <ScrollView data={skills} willExpand={false} onExpand={onExpand} />
    );
};
